@import "vars";

html,
body,
ul,
li,
button,
input,
textarea,
p,
h1,
h2 {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  outline: none;
}

li {
  list-style: none;
}

.link {
  text-decoration: none;
  transition: $hover-duration;
  &:hover {
    color: $active-color;
  }
}

button {
  border: none;
}
